<template>
  <el-dialog class="workbenches-edit" title="" :visible.sync="dialogFormVisible" width="75%">
    <el-form ref="form" :model="form" :rules="rules" label-width="130px" class="shot_add">
      <el-row :gutter="20">
        <el-col :span="8">
          <div class="grid-content">
            <!-- <el-form-item label=":" prop="field_id">
              <el-input v-model="form.field_id" placeholder="">
              </el-input>
            </el-form-item> -->
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item class="is-required" label=":" prop="image">
            <el-upload
                class="avatar-uploader"
                :action="uploadImageUrl"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload">
              <img v-if="form.task_image" :src="image" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
        </el-col>
      </el-row>
       <el-row :gutter="20">
        <el-col :span="8">
          <div class="grid-content">
            <el-form-item label=":" prop="task_priority_level">
              <el-input  v-model="form.task_byname" class="h-40 w-200"></el-input>
            </el-form-item>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content">
            <el-form-item label=":" prop="field_id">
              <el-select v-model="field_id" multiple placeholder="">
                <el-option v-for="item in options"
                  :key="item.id"
                  :label="item.real_name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="8">
          <div class="grid-content">
            <el-form-item label=":" prop="task_priority_level">
              <el-select v-model="form.task_priority_level" placeholder="" class="h-40 w-200">
                <el-option label="D" value="1"></el-option>
                <el-option label="C" value="2"></el-option>
                <el-option label="B" value="3"></el-option>
                <el-option label="A" value="4"></el-option>
              </el-select>
            </el-form-item>
          </div>
        </el-col>
         <el-col :span="8">
          <div class="grid-content">
            <el-form-item label=":" prop="difficulty">
              <el-select v-model="form.difficulty" placeholder="" class="h-40 w-200">
                <el-option label="D" value="1"></el-option>
                <el-option label="C" value="2"></el-option>
                <el-option label="B" value="3"></el-option>
                <el-option label="A" value="4"></el-option>
                <el-option label="S" value="5"></el-option> 
              </el-select>
            </el-form-item>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="24">
          <div class="grid-content">
            <el-form-item label=":" prop="plan_time" class="is-required">
              <el-date-picker
                v-model="plan_time"
                type="datetimerange"
                range-separator=""
                start-placeholder=""
                end-placeholder="">
            </el-date-picker>
            </el-form-item>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="20">
          <el-col :span="8">
          <div class="grid-content">
            <el-form-item label=":" prop="make_demand">
              <el-input type="textarea" :rows="3" placeholder="" v-model="form.make_demand"
                        class="h-40 w-200"></el-input>
            </el-form-item>
          </div>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible = !dialogFormVisible"> </el-button>
      <el-button type="primary" @click="edit('form')" :loading="isLoading"> </el-button>
    </div>
  </el-dialog>
</template>
<script>
import http from '../../../../assets/js/http'
import _g from '@/assets/js/global'

export default {
  data() {
    return{
        dialogFormVisible: false,
        isLoading: false,
        uploadImageUrl: window.HOST + '/admin/upload_image',
        plan_time:[],
        image:'',
        id:0,
        field_id:[],
        options:[],
        form: {
          group_id:null,//ID
          user_id:'',//
          project_id:2,//ID
          field_id:3,//	/ID
          shot_id:null,//ID 
          assets_id:null,//ID 
          tache_id:1,//ID
          tache_sort:null,//
          studio_id:2,//ID
          task_type:'',//
          task_image:'',//
          task_byname:'FUY',//
          make_demand:'',//
          shot_number:'001',//
          task_priority_level:'A',//
          difficulty:'S',//
          second_company:'',//(ID )
          task_is_status_time:'32',// 
          task_allocated_time:'9',// 
          plan_start_timestamp:'',//
          plan_end_timestamp:'2018/02/08 14:00',//
          actually_start_timestamp:'',//
          actually_end_timestamp:'',//	
          finish_degree:'',//
          task_status:1,// 1 5 10 15  16  20 25 30
          is_pause:1,// 1  2
          camera_model:'',//
          camera_catch:'',//	
          camera_motion:1,// 1
          camera_height:null,//
          camera_focus:'',//
          focus_distance:'',//
          depth_of_field:'',//
          pid:1,//
          create_time:111,//
          update_time:222,//
        },
        rules: {
          is_parse: [{required: true, message: ''}]
        }
    }
  },
  methods: {
      open() {
        this.dialogFormVisible = true
      },
      handleAvatarSuccess(res, file) {
        this.image = URL.createObjectURL(file.raw);
        this.form.task_image = res.data;
      },
      beforeAvatarUpload(file) {
        //  const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 0.195;
        //  if (!isJPG) {
        //    this.$message.error(' JPG !');
        //  }
        if (!isLt2M) {
          this.$message.error(' 200KB!');
        }
        return isLt2M;
      },
      //
      edit(form){
        this.form.plan_start_time = _g.j2time(this.plan_time[0])
        this.form.plan_end_time = _g.j2time(this.plan_time[1])
        this.form.difficulty = this.form.difficulty ? parseInt(this.form.difficulty) : 1
        this.form.task_priority_level = this.form.task_priority_level ? parseInt(this.form.task_priority_level) : 1
        this.form.user_id = this.field_id.join(',')
        this.form.task_image = this.form.task_image.slice(this.form.task_image.indexOf('uploads'))
        this.$refs[form].validate((valid) => {
          if (valid) {
          this.isLoading = !this.isLoading
          console.log(this.form)
          this.apiPut('admin/workbenches/', this.id, this.form ).then((res) => {
            this.handelResponse(res, (data) => {
            _g.toastMsg('success', '')
            setTimeout(() => {
              // this.goback()
               this.dialogFormVisible = false
               this.isLoading = !this.isLoading
            }, 1500)
            }, () => {
            this.isLoading = !this.isLoading
            })
          })
          }
        })
      }
  },
  mixins: [http],
  props: ['message'],
  watch: {
    message: function(data, o) {
        this.apiGet(`task/get_user?task_id=${data.id}`).then((res) => {
          this.handelResponse(res, (data) => {
            this.options = data.list
            console.log(data.list)
          })
        })
        this.id = data.id
        this.form=data
        this.form.task_image = this.image = window.baseUrl + '/' + data.task_image
        this.form.task_priority_level = data.task_priority_level.toString()
        this.form.difficulty = data.difficulty.toString()
        this.plan_time = [new Date(data.plan_start_timestamp * 1000), new Date(data.plan_end_timestamp * 1000)]
        
    }
  }
}
</script>
<style>
    .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .workbenches-edit .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .workbenches-edit .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .workbenches-edit .el-upload {
    width: 300px;
  }

  .workbenches-edit .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }


    .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .el-upload {
    width: 300px;
  }

  .shot_edit .el-tag__close{
    display: none;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    display: block;
    width: 100% !important;
    height: 100% !important;
  }
</style>




