<script>
  import fomrMixin from '@/assets/js/form_com'
  import http from '../../../../assets/js/http'
  import _g from '@/assets/js/global'
  import axios from 'axios'

  export default {
    data() {
      let shotNumber = (rule, value, callback) => {
        console.log(this.form.field_id)
        console.log(value)
        const data = {
          params: {
            field_id: parseInt(this.form.field_id),
            shot_number: value
          }
        }
        if(value == ''){
          callback(new Error(''));
        }else{
          axios.get('shot/check_num', data).then((res) => {
            console.log(res.data.code, 'suc')
            if (res.data.code === 400) {
              callback(new Error(res.data.error));
            }else{
              callback()
            }
          })
        }
//        /^[0-9]+$/

      };
      return {
        isArt: false,
        isModel: false,
        isMap: false,
        isBind: false,
        isTrack: false,
        isAni: false,
        isPaint: false,
        isSpec: false,
        isLight: false,
        isSynch: false,
        artOfStudio: [],
        modelOfStudio: [],
        mapOfStudio: [],
        bindOfStudio: [],
        trackOfStudio: [],
        animateOfStudio: [],
        paintOfStudio: [],
        specialOfStudio: [],
        lightOfStudio: [],
        synchOfStudio: [],
