        isImportShot: false,
        isLoading: false,
        uploadImageUrl: window.HOST + '/admin/upload_image',
        projectList: [],
        studiosList: [],
        fieldList: [],
        importForm: {
          project_id: '',  //id
          shot_file: ''    //
//          name: ''    ///
        },
        fileList: [],
        fieldForm: {
          project_id: '',  //id
          name: ''    ///
        },
        form: {
          project_id: '',    //id
          field_id: '',     //id
          asset_ids: '',    //id
          shot_image: '',    //
          shot_number: '',    //
          shot_byname: '',    //
          shot_name: '',    //
          time: '',    //
          ambient: '',    //
          plan_start_timestamp: '',    //
          plan_end_timestamp: '',    //
//          is_parse: '',    //   
//          is_assets: '',    //
          shot_explain: '',    //
          clip_frame_length: '',    //
          frame_range: '',    //
          priority_level: "1",    //
          difficulty: "1",    //
          handle_frame: '',    //
          material_frame_length: '',    //
          charge_speed_info: '',    //
          material_number: '',    //
          second_company: '',    //
          make_demand: '',    //
          status: 1    //
        },
        image: '',
        plan_time: '',
        frame_range1: '',
        frame_range2: '',
        handle_frame1: '',
        handle_frame2: '',
