          project_id: [{required: true, message: ''}],
          field_id: [{required: true, message: ''}],
          shot_image: [{required: true, message: ''}],
          shot_number: [{required: true, message: ''}, {min: 3, max: 6, message: '36'}, {
            pattern: /^[0-9]+$/,
            validator: shotNumber
          }],
          shot_byname: [
            {required: true, message: ''}, {pattern: /^[a-zA-Z]+$/, message: ''}
          ],
          shot_name: [{required: true, message: ''}, {pattern: /^[\u4E00-\u9FA5]+$/, message: ''}],
