<template>
  <div>
    <div class="m-b-20">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/admin' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/admin/projects/list' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="m-l-50 m-t-30 w-1000">
      <el-form ref="form" :model="form" :rules="rules" label-width="130px" class="project_add">
        <el-row :gutter="20">
          <el-form-item label=":" prop="project_name">
            <el-input v-model.trim="form.project_name" class="h-40 w-200"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="project_byname">
            <el-input v-model.trim="form.project_byname" class="h-40 w-200"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="movies_type">
            <el-select v-model="form.movies_type" placeholder="">
              <!--<el-option label="" value=""></el-option>-->
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-row>
        <el-row :gutter="20">
          <el-form-item class="is-required" label=":" prop="image">
            <!--  action="" -->
            <el-upload
                class="avatar-uploader"
                :action="uploadImageUrl"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload">
              <img v-if="form.project_image" :src="image" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
        </el-row>
        <el-row :gutter="20">
          <el-form-item label=":" prop="studio_ids" class="is-required">
            <el-select v-model="studio_ids" multiple placeholder="" class="form_studio_id">
              <el-option v-for="item in studiosOptions" :key="item.id" :label="item.name"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <!--<el-form-item label=":" prop="duration">-->
          <!--<el-input v-model.trim="form.duration" class="h-40 w-200"></el-input>-->
          <!--</el-form-item>-->
          <el-form-item label=":" prop="plan_time" class="is-required">
            <el-date-picker
                v-model="plan_time"
                type="datetimerange"
                range-separator=""
                start-placeholder=""
                end-placeholder="">
            </el-date-picker>
          </el-form-item>
        </el-row>
        <el-row :gutter="20">
          <el-form-item label=":" prop="aspect_ratio" class="is-required">
            <el-select v-model="form.aspect_ratio" placeholder="">
              <!--<el-option label="" value=""></el-option>-->
              <el-option label="169" value="1"></el-option>
              <el-option label="105" value="2"></el-option>
              <el-option label="11" value="3"></el-option>
            </el-select>
          </el-form-item>
          <!--<el-form-item label=":" prop="frame_sum">-->
          <!--<el-input v-model.trim="form.frame_sum" class="h-40 w-200"></el-input>-->
          <!--</el-form-item>-->
          <el-form-item label=":" prop="resolutic" class="is-required">
            <el-select v-model="form.resolutic" placeholder="">
              <!--<el-option label="" value=""></el-option>-->
              <el-option label="1024*768" value="1"></el-option>
              <el-option label="2560*1680" value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-row>
        <el-row :gutter="20">
          <el-form-item label=":" prop="frame_rate" class="is-required">
            <el-select v-model="form.frame_rate" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="24fps" value="1"></el-option>
              <el-option label="25fps" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="handle_frame" class="is-required">
            <el-input v-model.trim="form.handle_frame.handle_frame1" class="h-40" style="width: 100px;"></el-input>
            <el-input v-model.trim="form.handle_frame.handle_frame2" class="h-40" style="width: 100px;"></el-input>
          </el-form-item>
        </el-row>
        <el-row :gutter="20">
          <el-form-item label=":" prop="scene_director" class="is-required">
            <el-transfer
                :titles="['', '']"
                filterable
                :filter-method="filterMethod"
                filter-placeholder=""
                v-model="scene_director"
                :data="userList">
            </el-transfer>
          </el-form-item>
          <el-form-item label=":" prop="producer" class="is-required">
            <el-transfer
                :titles="['', '']"
                filterable
                :filter-method="filterMethod"
                filter-placeholder=""
                v-model="producer"
                :data="userList">
            </el-transfer>
          </el-form-item>
        </el-row>
        <el-row :gutter="20">
          <el-form-item label=":" prop="scene_producer" class="is-required">
            <el-transfer
                :titles="['', '']"
                filterable
                :filter-method="filterMethod"
                filter-placeholder=""
                v-model="scene_producer"
                :data="userList">
            </el-transfer>
          </el-form-item>
          <el-form-item label=":" prop="visual_effects_boss" class="is-required">
            <el-transfer
                :titles="['', '']"
                filterable
                :filter-method="filterMethod"
                filter-placeholder=""
                v-model="visual_effects_boss"
                :data="userList">
            </el-transfer>
          </el-form-item>
        </el-row>
        <el-row :gutter="20">
          <el-form-item label=":" prop="visual_effects_producer" class="is-required">
            <el-transfer
                :titles="['', '']"
                filterable
                :filter-method="filterMethod"
                filter-placeholder=""
                v-model="visual_effects_producer"
                :data="userList">
            </el-transfer>
          </el-form-item>
          <el-form-item label=":" prop="second_company_producer" class="is-required">
            <el-transfer
                :titles="['', '']"
                filterable
                :filter-method="filterMethod"
                filter-placeholder=""
                v-model="second_company_producer"
                :data="userList">
            </el-transfer>
          </el-form-item>
        </el-row>
        <el-row :gutter="20">
          <el-form-item label=":" prop="inside_coordinate" class="is-required">
            <el-transfer
                :titles="['', '']"
                filterable
                :filter-method="filterMethod"
                filter-placeholder=""
                v-model="inside_coordinate"
                :data="userList">
            </el-transfer>
          </el-form-item>
        </el-row>
        <el-row :gutter="20">
          <el-form-item label=":" prop="project_explain">
            <el-input
                type="textarea"
                :rows="2"
                v-model.trim="form.project_explain"
                class="h-40 w-800"
                style="resize: none;">
            </el-input>
          </el-form-item>
        </el-row>
        <el-form-item class="tsf_ProjectMap">
          <el-button type="primary" @click="add('form')" :loading="isLoading"></el-button>
          <el-button @click="goback()"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<style type="text/css">
  .form-checkbox:first-child {
    margin-left: 15px;
  }

  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .el-upload {
    width: 300px;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    /*width: 178px;*/
    /*height: 178px;*/
    display: block;
    width: 100% !important;
    height: 100% !important;
  }

  .project_add .el-form-item {
    display: inline-block;
  }

  .el-transfer-panel {
    width: 150px;
  }

  .el-transfer__buttons {
    padding: 0 15px;
  }

  .el-textarea__inner {
    resize: none;
  }

  .form_studio_id {
    width: 350px;
  }

  .block {
    /* margin-top: 20px; */
  }
</style>
<script>
  import fomrMixin from '@/assets/js/form_com'
  import http from '../../../../assets/js/http'
  import _g from '@/assets/js/global'

  export default {
    data() {
      return {
        isLoading: false,
        uploadImageUrl: window.HOST + '/admin/upload_image',
        userList: [],
        studiosOptions: [],
        plan_time: [],
        form: {
          project_name: "", //
          project_byname: '', //
          movies_type: '',//
          project_image: '', //
//          status: 0, //
//          duration: '',//
          aspect_ratio: '',//
//          frame_sum: '', //
          resolutic: '',//
          handle_frame: {
            handle_frame1: '',//1
            handle_frame2: '',//2
          },
//          screenings_count: "0",//
//          lens_count: "0",//
//          task_count: "0",//
          frame_rate: '',//
          plan_start_timestamp: "",//
          plan_end_timestamp: "",
          studio_ids: '', //
          scene_director: '',//
          producer: '',//
          scene_producer: '',//
          visual_effects_boss: '',//
          visual_effects_producer: '',//
          second_company_producer: '',//
          inside_coordinate: '',//
          project_explain: '',//
        },
        studio_ids: [], //
        scene_director: [],//
        producer: [],//
        scene_producer: [],//
        visual_effects_boss: [],//
        visual_effects_producer: [],//
        second_company_producer: [],//
        inside_coordinate: [],//
        image: '',
        filterMethod(query, item) {
          return item.pinyin.indexOf(query) > -1;
        },
        rules: {
          project_name: [
            {required: true, message: ''}
          ],
          movies_type: [
            {required: true, message: ''}
          ],
          project_byname: [
            {required: true, message: ''}, {min: 3, max: 5, message: ' 3  5 '}, {
              pattern: /^[A-Z]+$/,
              message: ''
            }
          ]
        }
      }
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.image = URL.createObjectURL(file.raw);
        this.form.project_image = res.data;
      },
      beforeAvatarUpload(file) {
        // const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 0.195;

        // if (!isJPG) {
        //   this.$message.error(' JPG !');
        // }
        if (!isLt2M) {
          this.$message.error(' 200KB!');
        }
        return isLt2M;
      },
      add(form) {
        if (!this.form.project_image) {
          _g.toastMsg('warning', '')
          return
        }
        if (!this.form.aspect_ratio) {
          _g.toastMsg('warning', '')
          return
        }
        if (!this.form.resolutic) {
          _g.toastMsg('warning', '')
          return
        }
        if (!this.form.frame_rate) {
          _g.toastMsg('warning', '')
          return
        }
        if (!this.form.handle_frame.handle_frame1 || !this.form.handle_frame.handle_frame2) {
          _g.toastMsg('warning', '')
          return
        }
        if (this.plan_time.length === 0) {
          _g.toastMsg('warning', '')
          return
        }
        if (this.studio_ids.length === 0) {
          _g.toastMsg('warning', '')
          return
        }
        if (this.scene_director.length === 0) {
          _g.toastMsg('warning', '')
          return
        }
        if (this.producer.length === 0) {
          _g.toastMsg('warning', '')
          return
        }
        if (this.scene_producer.length === 0) {
          _g.toastMsg('warning', '')
          return
        }
        if (this.visual_effects_boss.length === 0) {
          _g.toastMsg('warning', '')
          return
        }
        if (this.visual_effects_producer.length === 0) {
          _g.toastMsg('warning', '')
          return
        }
        if (this.second_company_producer.length === 0) {
          _g.toastMsg('warning', '')
          return
        }
        if (this.inside_coordinate.length === 0) {
          _g.toastMsg('warning', '')
          return
        }
        this.form.project_image = this.form.project_image.slice(this.form.project_image.indexOf('uploads'))
        this.form.handle_frame = this.form.handle_frame.handle_frame1 + ',' + this.form.handle_frame.handle_frame2
        this.form.movies_type = parseInt(this.form.movies_type) ? parseInt(this.form.movies_type) : ''
        this.form.resolutic = parseInt(this.form.resolutic) ? parseInt(this.form.resolutic) : ''
        this.form.frame_rate = parseInt(this.form.frame_rate) ? parseInt(this.form.frame_rate) : ''
        this.form.aspect_ratio = parseInt(this.form.aspect_ratio) ? parseInt(this.form.aspect_ratio) : ''
        this.form.studio_ids = this.studio_ids.join(',')
        this.form.scene_director = this.scene_director.join(',')
        this.form.producer = this.producer.join(',')
        this.form.scene_producer = this.scene_producer.join(',')
        this.form.visual_effects_boss = this.visual_effects_boss.join(',')
        this.form.visual_effects_producer = this.visual_effects_producer.join(',')
        this.form.second_company_producer = this.second_company_producer.join(',')
        this.form.inside_coordinate = this.inside_coordinate.join(',')
        this.form.plan_start_timestamp = _g.j2time(this.plan_time[0])
        this.form.plan_end_timestamp = _g.j2time(this.plan_time[1])

        this.$refs.form.validate((pass) => {
          if (pass) {
            this.isLoading = !this.isLoading
            this.apiPut('admin/projects/', this.id, this.form).then((res) => {
              this.handelResponse(res, (data) => {
                _g.toastMsg('success', '')
                setTimeout(() => {
                  this.goback()
                }, 1500)
              }, () => {
                this.isLoading = !this.isLoading
              })
            })
          }
        })
      },
      //
      getAllStudios() {
        return new Promise((resolve, reject) => {
          this.apiGet('admin/studios').then((res) => {
            this.handelResponse(res, (data) => {
              resolve(data.list)
            })
          })
        })
      },
      getAllUsers() {
        return new Promise((resolve, reject) => {
          this.apiGet('admin/users').then((res) => {
            this.handelResponse(res, (data) => {
              const temp = [];
              (data.list).forEach((name, index) => {
                temp.push({
                  label: name.realname,
                  key: name.id,
                  pinyin: name.username
                });
              });
              resolve(temp)
            })
          })
        })
      },
      async getCompleteData() {
        this.userList = await this.getAllUsers()
        this.studiosOptions = await this.getAllStudios()
        this.apiGet('admin/projects/' + this.id).then((res) => {
          this.handelResponse(res, (data) => {
            this.form.project_name = data.project_name
            this.form.project_byname = data.project_byname
            this.form.project_explain = data.project_explain
            this.form.project_image = this.image = window.baseUrl + '/' + data.project_image
            this.form.handle_frame.handle_frame1 = data.handle_frame.split(',')[0]
            this.form.handle_frame.handle_frame2 = data.handle_frame.split(',')[1]
            this.form.movies_type = data.movies_type.toString()
            this.form.resolutic = data.resolutic.toString()
            this.form.frame_rate = data.frame_rate.toString()
            this.form.aspect_ratio = data.aspect_ratio.toString()

            function str2num(str) {
              let arr = str.split(',')
              let temp = []
              _(arr).forEach((key, index) => {
                temp.push(parseInt(key))
              })
              return temp
            }

            this.studio_ids = str2num(data.studio_ids)
            this.scene_director = str2num(data.scene_director)
            this.producer = str2num(data.producer)
            this.scene_producer = str2num(data.scene_producer)
            this.visual_effects_boss = str2num(data.visual_effects_boss)
            this.visual_effects_producer = str2num(data.visual_effects_producer)
            this.second_company_producer = str2num(data.second_company_producer)
            this.inside_coordinate = str2num(data.inside_coordinate)
            this.plan_time = [new Date(data.plan_start_timestamp * 1000), new Date(data.plan_end_timestamp * 1000)]
          })
        })
      }
    },
    created() {
      this.id = this.$route.params.id
      this.getCompleteData()
    },
    mixins: [http, fomrMixin]
  }
</script>


